@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700|Poppins:700");
@import url("https://fonts.googleapis.com/css?family=Merriweather:700,900");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  color: #111111;
  font-size: 16px;
  line-height: 1.6;
  font-family: 'Open Sans', sans-serif;
}

a {
  text-decoration: none;
  color: #111111;
  transition: all 200ms ease-in-out;
}

.ch-hero {
  height: 100vh;
  padding: 40px 70px 59px;
  background-size: cover;
  background-position: left center;
  background-repeat: no-repeat;
  background-image: url(https://images.unsplash.com/photo-1511025998370-7d59f82e9c8f?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=dc24c2b7ca065311bf9de359a767959c&auto=format&fit=crop&w=1036&q=80);
}

.ch-hero--logo {
  font-family: 'Poppins', 'Open Sans', sans-serif;
  font-weight: 700;
  font-size: 24px;
}

.ch-hero--logo:hover {
  color: #FF4136;
}

.ch-hero--author {
  font-size: 14px;
  font-weight: 600;
}

.ch-hero--title {
  font-size: 40px;
  font-family: 'Merriweather', serif;
  font-weight: 900;
  letter-spacing: 1px;
  line-height: 1.3;
  margin: 5px 0 25px;
}

.ch-hero--paragraph {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 70px;
}

.ch-hero--cta {
  padding: 0.5em 2em;
  border: 1px solid #FF4136;
  color: #FF4136;
  border-radius: 25px;
  transition: all 200ms ease-in-out;
}

.ch-hero--cta:hover {
  background: #FF4136;
  color: #fff;
}

.ch-hero--links {
  margin-bottom: 45px;
}

.ch-hero--accent {
  background: #FF4136;
  color: #fff;
  margin-right: 15px;
  padding: 0.5em 2.5em;
}

.ch-hero--pagination__item {
  width: 10px;
  height: 10px;
  background: #111111;
  border-radius: 50%;
  margin-right: 10px;
  cursor: pointer;
  transition: all 300ms ease-in-out;
}

.ch-hero--pagination__item.ch-active {
  width: 45px;
  border-radius: 25px;
}

.ch-hero--content {
  max-width: 40%;
}

.flex {
  display: flex;
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-space--between {
  justify-content: space-between;
}

.flex-space--around {
  justify-content: space-around;
}

.flex-align--center {
  align-items: center;
}

.flex-align--end {
  align-items: flex-end;
}

.flex-direction--column {
  flex-direction: column;
}

.flex-end {
  justify-content: flex-end;
}
